<script>
import Cell from "./components/Cell";
export default {
  components: { Cell },
  data() {
    return {
      n: 1,
      flag: 0,
      map: [
        [null, null, null, null, null, null, null, null, null, null],
        [null, null, null, null, null, null, null, null, null, null],
        [null, null, null, null, null, null, null, null, null, null],
        [null, null, null, null, null, null, null, null, null, null],
        [null, null, null, null, null, null, null, null, null, null],
        [null, null, null, null, null, null, null, null, null, null],
        [null, null, null, null, null, null, null, null, null, null],
        [null, null, null, null, null, null, null, null, null, null],
        [null, null, null, null, null, null, null, null, null, null],
        [null, null, null, null, null, null, null, null, null, null],
      ],
      turn: "X",
      result: "no one win now!!! ",
    };
  },
  methods: {
    greet(i, test) {
      if (this.map[Math.floor(i / 10)][i % 10] != null) {
        return;
      }
      if (this.flag == 1) {
        this.end1();
        return;
      }
      console.log(`${i} is click  is ${test}`);
      this.map[Math.floor(i / 10)][i % 10] = test;
      this.n = this.n + 1;
      this.tell();
      if (test == "X") {
        this.turn = "O";
      } else {
        this.turn = "X";
      }
    },
    tell() {
      const map = this.map;
      // this.result = map[i][j] + " is win";
      for (let xi = 0; xi < 10; xi++) {
        for (let j = 0; j < 10; j++) {
          if (map[xi][j] == null) {
            continue;
          }
          let ji = 0;
          while (ji < 5 && xi + ji < 10) {
            ji++;
            if (map[xi][j] != map[xi + ji][j]) {
              break;
            }
            if (ji == 4) {
              this.result = map[xi][j] + " is winner";
              this.end();
            }
          }
          ji = 0;
          while (ji < 5 && j + ji < 10) {
            ji++;
            if (map[xi][j] != map[xi][j + ji]) {
              break;
            }
            if (ji == 4) {
              this.result = map[xi][j] + " is winner";
              this.end();
            }
          }
          ji = 0;
          while (ji < 5 && j + ji < 10 && xi + ji < 10) {
            ji++;
            if (map[xi][j] != map[xi + ji][j + ji]) {
              break;
            }
            if (ji == 4) {
              this.result = map[xi][j] + " is winner";
              this.end();
            }
          }
          ji = 0;
          while (ji < 5 && j - ji >= 0 && xi + ji < 10) {
            ji++;
            if (map[xi][j] != map[xi + ji][j - ji]) {
              break;
            }
            if (ji == 4) {
              this.result = map[xi][j] + " is winner";
              this.end();
            }
          }
        }
      }
    },
    end() {
      this.flag = 1;
      alert(this.result);
    },
    end1() {
      alert("game is over\n if you want next game?");
      window.location.reload();
    },
  },
};
</script>

